<template>
  <div class="container">
    <div class="markdown-view">
      <mavon-editor
        :value="mdText"
        :editable="false"
        :toolbarsFlag="false"
        :subfield="false"
        defaultOpen="preview"
        :navigation="true"
      />
    </div>
  </div>
</template>
<script>
import { details } from "../../../public/config/detail";
export default {
  data() {
    return {
      detailId: 0,
      mdText: '',
    };
  },
  created() {
    this.detailId = this.$route.params.id;
    this.mdText = details[this.detailId].replaceAll('-=', '```');
  },
};
</script>
<style lang='less' scoped>
.container{
  background:white;
  min-height:100vh;
  .markdown-view{
    margin:0 auto;
    width:65%;
  }
}
</style>